import React,{useContext} from 'react';
import Pizza from './Pizza'
import {CartContext} from './Cart';


const ContextCart = () => {
    const {item} = useContext(CartContext);
    
    return(
        <>
        <div className="shop">
        <p></p>
        <i class="fas fa-shopping-cart"></i>
        </div>

        <section className="main-cart-section">
        <center><h1>PIZZA ORDER</h1></center>
        <p>You have <span className="total-items count"></span> items in shopping cart </p>
        <div className="items">
        {
            item.map((curItem)=>{
              return <Pizza Key={ curItem.id} {...curItem} />
            })
        }
        
        </div>
        </section>
        <footer>
        <h2>TOTAL:</h2>
        <button class="btn btn-primary">CheckOut</button>
        </footer>
        </>

    );

}

export default ContextCart;