export const reducer = (state,action) =>{
    
    if(action.type=== "GET_TOTAL")
    {
     let {totalItem} = state.item.reduce((accum,currVal) => {
         let {quantity} = currVal;
         accum.totalItem += quantity;
         return accum;  
    },
     {
        totalItem:0,
     }) ;
     return {...state , totalItem};  
    }
    return state;

};