import React from 'react'
import { FirstName } from './Navbar';


const Home = () =>{
    return(
        
        <FirstName.Consumer>
        {
            (value)=>{
           return( 
           <p className="abc">aniket{value}</p>
           );
        }
        
    }
    </FirstName.Consumer>
        
        
        
    )
}

export default Home